import fetchJsonp from 'fetch-jsonp'
import CONFIG from '../../CONFIG'

/**
 * URL
 *
 * @param    {string}  type        or more...
 * @param    {string}  text        
 * @param    {number}  start       offset
 *
 * @return   {string}              URL
 */
function getURL (type, text, start) {
  const pageInfo = getPageInfo(type)
  const searchURL = pageInfo.searchPage.searchURL
  const offset = start || 0

  return searchURL + `?q=${text}&start=${offset}&count=${CONFIG.searchListCount}`
}

/**
 * 
 */

/**
 * 
 *
 * : , pageList
 *
 * @param    {string}    pageName       or more...
 * @return   {object}                   
 */
export function getPageInfo (pageName) {
  const pageList = CONFIG.page

  for (let i = 0; i < pageList.length; i++) {
    if (pageName === pageList[i].pageName) {
      return pageList[i]
    }
  }

  console.log(new Error('PageName Is Wrong!!! Please Check It In CONFIG!!!'))

  return pageList[0]
}

/**
 * 
 *
 * @return   {array}                    
 */
export function getAllPageName () {
  const pageNameArr = []

  for (var i = 0; i < CONFIG.page.length; i++) {
    pageNameArr.push(CONFIG.page[i].pageName)
  }

  return pageNameArr
}

/**
 * 
 *
 * @return   {string}                    
 */
export function getActivePageName (pageName) {
  const activePageName = pageName || CONFIG.activePageName

  return getPageInfo(activePageName).pageName
}

/**
 * placeholder
 *
 * @param    {string}  searchType        or more...
 * @return   {string}                    placeholder
 */
export function getPlaceholder (searchType) {
  const pageInfo = getPageInfo(searchType)

  return pageInfo.searchPage.searchPlaceholder
}

/**
 * json
 *
 * @param    {string}  type        or more...
 * @param    {string}  text        
 * @param    {number}  start       offset
 * @param    {func}    callback    
 * @return   json
 */
export function getJson (type, text, start, callback) {
  const URL = getURL(type, text, start) // url

  fetchJsonp(URL)
    .then(response => {
      return response.json()
    })
    .then(json => {
      callback(json)
    })
    .catch(err => {
      console.log(err)
    })
}
