import React, { Component } from 'react'

import './index.css'

class Item extends Component {
  // : footer, 
  handleClick (item, page) {
    this.props.setActiveItem(item)
    this.props.setPage(page)
    window.setTimeout(() => { this.props.setScrollY() }, 0) //   
  }

  render () {
    const item = this.props.page.pageName // item
    const icon = this.props.page.icon // icon
    const activeItem = this.props.activeItem // item

    const page = item // 
    const isActive = item === activeItem // item
    const activeCls = isActive ? 'active-item' : '' // 

    return (
      <div
        className={'footer-item ' + activeCls}
        onClick={this.handleClick.bind(this, item, page)}
      >
        {/* footer-item */}
        <i className={'footer-item-icon iconfont icon-' + icon} />

        <div className='footer-item-text'>{item}</div>
      </div>
    )
  }
}

export default Item
