import React, {Component} from 'react'
import LazyLoad from 'react-lazyload'
import CONFIG from '../../../../CONFIG'

import './index.css'

class Content extends Component {
  render () {
    const activeSearchPage = this.props.activeSearchPage // searchpage
    const detailPageInfo = this.props.detailPageInfo // 
    const pageState = this.props.pageState //  searchpage  detailpage

    //   detailpage   
    if (!Object.keys(detailPageInfo).length || pageState !== 'detailPage') {
      return (
        <div>{CONFIG.pageLoading.text}</div>
      )
    }

    // 
    const img = detailPageInfo.image || detailPageInfo.images.small // img
    const nullImg = CONFIG.nullImg // 
    const title = detailPageInfo.title || '' // tt
    const rating = detailPageInfo.rating.average || '' // rating
    const tags = detailPageInfo.tags || [] // tags

    //  
    switch (activeSearchPage) {
      case CONFIG.page[0].pageName:
        const authorArr = detailPageInfo.author.length ? detailPageInfo.author : ['']
        const publisher = detailPageInfo.publisher || ''
        const pubdate = detailPageInfo.pubdate || ''
        const price = detailPageInfo.price || ''
        const catalog = detailPageInfo.catalog || ''
        const summary = detailPageInfo.summary || ''

        return (
          <div className='content--book'>

            <div className='content-abstract-container'>
              <div className='img-wrapper'>
                <LazyLoad height={'100%'}>
                  <img src={img} alt='' className='abstract-img' />
                </LazyLoad>
              </div>

              <div className='info-wrapper'>
                <div className='info-tt'>: {title}</div>

                <div>
                  <span className='info-author'>: </span>
                  {authorArr.map((item, index) => {
                    return (
                      <span key={index}>item</span>
                    )
                  })}
                </div>

                <div className='info-publisher'>: {publisher}</div>

                <div className='info-pubdate'>: {pubdate}</div>

                <div className='info-rating'>: {rating}</div>

                <div className='info-price'>: {price}</div>

                <div className='info-tags'>
                  {tags.map((item, index) => {
                    if (index < 3) {
                      return (
                        <span className='tags-item' key={index}>
                          {item.name}
                        </span>
                      )
                    }
                  })
                  }
                </div>
              </div>
            </div>

            <div className='content-detail-container'>

              <div className='detail-catalog'>
                <h1 className='detail-tt'></h1>
                <p className='detail-p'>{catalog}</p>
              </div>

              <div className='detail-summary'>
                <h1 className='detail-tt'></h1>
                <p className='detail-p'>{summary}</p>
              </div>
            </div>
          </div>
        )

      case CONFIG.page[1].pageName:
        const genres = detailPageInfo.genres || []
        const year = detailPageInfo.year || ''
        const directorsArr = detailPageInfo.directors.length ? detailPageInfo.directors : [{name: ''}]
        const originalTitle = detailPageInfo.original_title || ''
        const casts = detailPageInfo.casts.length ? detailPageInfo.casts : [{avatars: {small: nullImg.src}}]

        return (
          <div className='content--movie'>

            <div className='content-abstract-container'>

              <div className='img-wrapper'>
                <LazyLoad height={'100%'}>
                  <img src={img} alt='' className='abstract-img' />
                </LazyLoad>
              </div>
            </div>

            <div className='content-detail-container'>

              <div className='detail-summary'>

                <h1 className='detail-tt'></h1>

                <div className='info-tt'>
                  <span>: {title} </span>
                  <span className='info-tags'>
                    {genres.map((item, index) => {
                      if (index < 3) {
                        return (
                          <span className='tags-item' key={index}>
                            {item}
                          </span>
                        )
                      }
                    })}
                  </span>
                </div>

                <div className='info-year'>: {year}</div>

                <div className='info-directors-wrapper'>
                  <span>: </span>
                  {directorsArr.map((item, index) => {
                    return (
                      <span key={index} className='info-directors'>{`${item.name}  `}</span>
                    )
                  })}
                </div>

                <div className='info-original-tt'>{`${title}(${originalTitle})`}</div>
              </div>

              <div className='detail-casts-container'>

                <h1 className='detail-tt'></h1>
                <div className='detail-casts-avartar-container'>
                  {casts.map((item, index) => {
                    return (
                      <div key={index} className='casts-avatar-wrapper'>
                        <LazyLoad height={'100%'}>
                          <img src={item.avatars ? item.avatars.small : nullImg.src} alt='' className='casts-avatar' />
                        </LazyLoad>
                      </div>
                    )
                  })}
                </div>
              </div>
            </div>
          </div>
        )

      case CONFIG.page[2].pageName:
        const authorMusic = detailPageInfo.author ? detailPageInfo.author[0] : {name: ''}
        const publisherMusic = detailPageInfo.attrs.publisher ? detailPageInfo.attrs.publisher[0] : ''
        const pubdateMusic = detailPageInfo.attrs.pubdate ? detailPageInfo.attrs.pubdate[0] : ''
        const tracksArr = detailPageInfo.attrs.tracks ? detailPageInfo.attrs.tracks : ['']

        return (
          <div className='content--music'>
            <div className='content-abstract-container'>
              <div className='img-wrapper'>
                <LazyLoad height={'100%'}>
                  <img src={img} alt='' className='abstract-img' />
                </LazyLoad>
              </div>

              <div className='info-wrapper'>
                <div className='info-tt'>
                  <span>: {title} </span>
                  <span className='info-tags'>
                    {tags.map((item, index) => {
                      return (
                        <span className='tags-item' key={index}>
                          {item.name}
                        </span>
                      )
                    })
                    }
                  </span>
                </div>

                <div className='info-author'>: {authorMusic.name}</div>

                <div className='info-publisher'>: {publisherMusic}</div>

                <div className='info-pubdate'>: {pubdateMusic}</div>

                <div className='info-rating'>: {rating}</div>
              </div>
            </div>

            <div className='content-detail-container'>
              <div className='detail-summary'>
                <h1 className='detail-tt'></h1>
              </div>

              <div className='detail-tracks'>
                <h1 className='detail-tt'></h1>
                {tracksArr.map((item, index) => {
                  return (
                    <div key={index} className='tracks-item'>{item}</div>
                  )
                })}
              </div>
            </div>

          </div>
        )

      default:
        return (
          <div>wrong page...</div>
        )
    }
  }
}

export default Content
