import React, { Component } from 'react'
import CONFIG from '../../../../CONFIG'
import {getActivePageName} from '../../../util/util'

import './index.css'

import Item from './item'

class List extends Component {
  // 
  showDetailPage (item) {
    this.props.showDetailPage(item)
  }

  render () {
    const activePage = this.props.page // 
    const arr = this.props.arr[getActivePageName(activePage)] // 

    const pageLoadingText = CONFIG.pageLoading.text // 
    const noSearchResource = CONFIG.noSearchResource.text // 

    // if undefined  else if 0  
    if (arr === undefined) {
      return (
        <div className='loading-wrapper'>
          <div className='loading'>{pageLoadingText}</div>
        </div>
      )
    } else if (!arr.length) {
      return (
        <div className='none-wrapper'>
          <div className='none'>{noSearchResource}</div>
        </div>
      )
    }

    return (
      <div className='list'>
        {/* List Item */}
        {arr.map((item, index) => {
          return (
            <Item
              item={item}
              key={index}
              showDetailPage={this.showDetailPage.bind(this)}
              activePage={activePage}
            />
          )
        })}
      </div>
    )
  }
}

export default List
