import React, { Component } from 'react'
import CONFIG from '../../../../CONFIG'
import {getActivePageName, getAllPageName, getPageInfo, getJson} from '../../../util/util'

import './index.css'

import Search from '../../components/search'
import List from '../../components/list'
import Footer from '../../components/footer'

class SearchPage extends Component {
  constructor () {
    super()

    this.state = {
      searchType: getActivePageName(), // 
      searchText: '', // 
      arr: {}, // : arr: {'': ..., '': ..., '': ... or more...}
      startY: 0, // y
      moveY: 0, // y
      refreshState: {}, // , arr
      loadmoreState: {}, // , arr
      loadmoreSwitch: {}, // , arr
      listStartOffset: {}, // offset, arr
      scrollY: {} // y, arr
    }

    this.initList()
  }

  //    
  initList () {
    const pageNameArr = getAllPageName()

    for (var i = 0; i < pageNameArr.length; i++) {
      const key = pageNameArr[i]
      const arr = this.state.arr
      const defaultSearchArr = getPageInfo(key).searchPage.defaultSearch
      const defaultSearch = defaultSearchArr[0]

      const refreshState = this.state.refreshState
      const loadmoreState = this.state.loadmoreState
      const loadmoreSwitch = this.state.loadmoreSwitch
      const listStartOffset = this.state.listStartOffset
      const scrollY = this.state.scrollY

      refreshState[key] = CONFIG.refresh.state.prepare
      loadmoreState[key] = CONFIG.loadmore.state.prepare
      loadmoreSwitch[key] = true
      listStartOffset[key] = 0
      scrollY[key] = 0

      getJson(key, defaultSearch, 0, json => {
        const searchResultArrName = getPageInfo(key).searchPage.searchResultArrName
        arr[key] = json[searchResultArrName]
        this.setState({
          arr: arr,
          refreshState: refreshState,
          loadmoreState: loadmoreState,
          loadmoreSwitch: loadmoreSwitch,
          listStartOffset: listStartOffset,
          scrollY: scrollY
        })
      })
    }
  }

  // this.state.arrkey
  onSubmit (searchResultArr) {
    const activePage = this.props.activeSearchPage

    const arr = this.state.arr
    const loadmoreState = this.state.loadmoreState
    const loadmoreSwitch = this.state.loadmoreSwitch
    const offset = this.state.listStartOffset

    arr[getActivePageName(activePage)] = searchResultArr
    loadmoreState[activePage] = CONFIG.loadmore.state.prepare
    loadmoreSwitch[activePage] = true
    offset[activePage] = 0

    this.setState({
      arr: arr,
      loadmoreSwitch: loadmoreSwitch,
      listStartOffset: offset
    })
  }

  // 
  onSubmitSearchText (str) {
    this.setState({
      searchText: str
    })
  }
  // 
  showDetailPage (item) {
    this.props.showDetailPage(item)
  }

  // 
  setPage (page) {
    this.props.setActiveSearchPage(page)

    this.setState({
      searchType: page
    })
  }

  // 
  handleTouchStart (e) {
    const touch = e.touches[0]
    const startY = touch.pageY

    this.setState({
      startY: startY
    })
  }

  // 
  handleTouchMove (e) {
    const touch = e.touches[0]
    const moveY = touch.pageY - this.state.startY // moveY
    const key = this.state.searchType

    //    
    if (window.scrollY === 0 && moveY >= 0) {
      e.preventDefault()

      this.refs.listWrapper.style.top = moveY + 'px'

      this.setState({
        moveY: moveY
      })

      // if 40px, , ?????? ,else 
      if (moveY >= 40) {
        const refreshState = this.state.refreshState

        refreshState[key] = CONFIG.refresh.state.start

        this.setState({
          refreshState: refreshState
        })

        if (moveY >= 0.7 * window.innerHeight) {
          this.refs.egg.style.display = 'block'
        }
      } else {
        const key = this.state.searchType
        const refreshState = this.state.refreshState

        refreshState[key] = CONFIG.refresh.state.prepare

        this.setState({
          refreshState: refreshState
        })
      }
    }

    //   true, 
    if (window.scrollY + window.innerHeight === document.body.scrollHeight && this.state.loadmoreSwitch[key] === true) {
      const searchType = this.state.searchType // 
      const defaultSearchArr = getPageInfo(searchType).searchPage.defaultSearch // 
      const rdmDefaultSearch = defaultSearchArr[Math.floor(Math.random() * defaultSearchArr.length)] // 
      const searchText = this.state.searchText || rdmDefaultSearch // 
      const offset = this.state.listStartOffset // offset
      const loadmoreState = this.state.loadmoreState
      const loadmoreSwitch = this.state.loadmoreSwitch
      const arr = this.state.arr // 

      offset[searchType] = offset[searchType] + CONFIG.searchListCount
      loadmoreState[searchType] = CONFIG.loadmore.state.loading
      loadmoreSwitch[searchType] = false

      this.setState({
        listStartOffset: offset,
        loadmoreState: loadmoreState,
        loadmoreSwitch: loadmoreSwitch
      })

      getJson(searchType, searchText, this.state.listStartOffset[searchType], json => {
        if (this.state.listStartOffset[searchType] + CONFIG.searchListCount >= json.total) {
          loadmoreState[searchType] = CONFIG.loadmore.state.loadAll
          loadmoreSwitch[searchType] = false

          this.setState({
            loadmoreState: loadmoreState,
            loadmoreSwitch: loadmoreSwitch
          })
        } else {
          const searchResultArrName = getPageInfo(searchType).searchPage.searchResultArrName

          arr[searchType] = arr[searchType].concat(json[searchResultArrName])
          loadmoreState[searchType] = CONFIG.loadmore.state.prepare
          loadmoreSwitch[searchType] = true

          this.setState({
            arr: arr,
            loadmoreState: loadmoreState,
            loadmoreSwitch: loadmoreSwitch
          })
        }
      })
    }
  }

  // 
  handleTouchEnd (e) {
    const searchType = this.state.searchType
    const refreshState = this.state.refreshState
    const scrollY = this.state.scrollY

    scrollY[searchType] = window.scrollY

    this.props.getSearchPageScrollY(scrollY)

    this.setState({
      scrollY: scrollY
    })

    // if prepare else if start
    if (refreshState[searchType] === CONFIG.refresh.state.prepare) {
      this.refs.listWrapper.style.top = 0
    } else if (refreshState[searchType] === CONFIG.refresh.state.start) {
      const defaultSearchArr = getPageInfo(searchType).searchPage.defaultSearch // 
      const rdmDefaultSearch = defaultSearchArr[Math.floor(Math.random() * defaultSearchArr.length)] // 
      const searchText = this.state.searchText || rdmDefaultSearch // 
      const arr = this.state.arr // 
      const offset = this.state.listStartOffset

      refreshState[searchType] = CONFIG.refresh.state.refreshing
      offset[searchType] = 0

      this.setState({
        refreshState: refreshState,
        listStartOffset: offset
      })

      this.refs.listWrapper.style.top = 40 + 'px'

      getJson(searchType, searchText, this.state.listStartOffset[searchType], json => {
        if (this.state.listStartOffset[searchType] + CONFIG.searchListCount < json.total) {
          const loadmoreState = this.state.loadmoreState
          const loadmoreSwitch = this.state.loadmoreSwitch

          loadmoreState[searchType] = CONFIG.loadmore.state.prepare
          loadmoreSwitch[searchType] = true

          this.setState({
            loadmoreState: loadmoreState,
            loadmoreSwitch: loadmoreSwitch
          })
        }

        const searchResultArrName = getPageInfo(searchType).searchPage.searchResultArrName

        arr[searchType] = json[searchResultArrName]

        refreshState[searchType] = CONFIG.refresh.state.complete

        this.setState({
          arr: arr,
          refreshState: refreshState
        })

        // 500msrefreshState
        window.setTimeout(() => {
          this.refs.listWrapper.style.top = 0

          refreshState[searchType] = CONFIG.refresh.state.prepare

          this.setState({
            refreshState: refreshState
          })
        }, 500)
      })
    }

    this.refs.egg.style.display = 'none'
  }

  // 
  goToTop () {
    const key = this.state.searchType
    const scrollY = this.state.scrollY

    scrollY[key] = 0

    this.setState({
      scrollY: scrollY
    })

    window.scrollTo(0, 0)
  }

  // 
  componentDidMount () {
    const listContainer = this.refs.listContainer

    listContainer.addEventListener('touchstart', this.handleTouchStart.bind(this), false)
    listContainer.addEventListener('touchmove', this.handleTouchMove.bind(this), false)
    listContainer.addEventListener('touchend', this.handleTouchEnd.bind(this), false)
  }

  // 
  componentWillUnmount () {
    const listContainer = this.refs.listContainer

    listContainer.removeEventListener('touchstart', this.handleTouchStart.bind(this))
    listContainer.removeEventListener('touchmove', this.handleTouchMove.bind(this))
    listContainer.removeEventListener('touchend', this.handleTouchEnd.bind(this))
  }

  render () {
    const activePage = this.props.activeSearchPage // 

    const refreshText = this.state.refreshState[activePage].text // 
    const loadmoreText = this.state.loadmoreState[activePage].text // 

    return (
      <div className='searchpage'>
        {/* Searchpage Search  List   Footer */}

        {/*   Search  List  */}
        <div className='search-container' ref='searchContainer'>
          {/* Search */}
          <Search
            searchType={this.state.searchType}
            onSubmit={this.onSubmit.bind(this)}
            onSubmitSearchText={this.onSubmitSearchText.bind(this)}
          />
          {/*    List  */}
          <div className='list-container' ref='listContainer'>
            {/*  */}
            <div className='refresh' ref='refresh'>
              <div className='refresh-txt'>{refreshText}</div>
              <div className='egg' ref='egg'>
                <div>Congratulations!!!You have just found the Easter egg!!!</div>
                <div>autor: </div>
                <div>email: 1422975258@qq.com</div>
              </div>
            </div>
            {/* List */}
            <div className='list-wrapper' ref='listWrapper'>
              <List
                page={activePage}
                arr={this.state.arr}
                showDetailPage={this.showDetailPage.bind(this)}
              />
            </div>
            {/*  */}
            <div className='loadmore' ref='loadmore'>{loadmoreText}</div>
          </div>
        </div>
        {/*  */}
        <i className='gototop iconfont icon-top' onClick={this.goToTop.bind(this)} />
        {/* Footer */}
        <Footer setPage={this.setPage.bind(this)} scrollY={this.state.scrollY} />
      </div>
    )
  }
}

export default SearchPage
