import React, { Component } from 'react'
import {getPageInfo} from '../../../util/util'

import './index.css'

class Header extends Component {
  // , 
  handleClick () {
    const key = this.props.activeSearchPage
    const searchPageScrollY = this.props.searchPageScrollY

    this.props.showSearchPage()

    window.setTimeout(() => { window.scrollTo(0, searchPageScrollY[key]) }, 0)
  }

  render () {
    const activeSearchPage = this.props.activeSearchPage // 

    const detailPageInfo = getPageInfo(activeSearchPage).detailPage // 
    const returnBtnTxt = detailPageInfo.returnBtnTxt // 
    const headerText = detailPageInfo.headerText // 

    return (
      <div className='header'>
        {/* Header */}
        <div
          onClick={this.handleClick.bind(this)}
          className='header-btn'
        >
          <i className='iconfont icon-back' />
          <span>{returnBtnTxt}</span>
        </div>
        <div className='header-text'>{headerText}</div>
      </div>
    )
  }
}

export default Header
