import React, { Component } from 'react'
import {getPlaceholder, getPageInfo, getJson} from '../../../util/util'

import './index.css'

class Search extends Component {
  constructor () {
    super()
    this.state = {
      searchText: '' // 
    }
  }

  // KeyUp, , EntersearchEvent
  handleKeyUpEvent (e) {
    const searchText = e.target.value
    const keyCode = e.keyCode

    // , statesearchTextkeyup, e.target.valuestate.searchText
    window.setTimeout(() => {
      this.setState({
        searchText: searchText
      })

      if (keyCode === 13) {
        const searchType = this.props.searchType // 
        const defaultSearchArr = getPageInfo(searchType).searchPage.defaultSearch // 
        const rdmDefaultSearch = defaultSearchArr[Math.floor(Math.random() * defaultSearchArr.length)] // 
        const searchText = this.state.searchText || rdmDefaultSearch // , 

        this.searchEvent(searchType, searchText, 0)
      }
    }, 0)
  }

  // , json
  searchEvent (searchType, searchText, start) {
    this.props.onSubmitSearchText(this.state.searchText)

    getJson(searchType, searchText, start, json => {
      const searchResultArrName = getPageInfo(searchType).searchPage.searchResultArrName
      this.props.onSubmit(json[searchResultArrName])
    })
  }

  render () {
    const searchType = this.props.searchType // 

    const placeholder = getPlaceholder(searchType) // placeholder
    const defaultSearchArr = getPageInfo(searchType).searchPage.defaultSearch // 
    const rdmDefaultSearch = defaultSearchArr[Math.floor(Math.random() * defaultSearchArr.length)] // 
    const searchText = this.state.searchText || rdmDefaultSearch // , 

    return (
      <div className='search-wrapper'>
        {/* Search  */}
        <input
          type='text'
          className='search-input'
          placeholder={placeholder}
          onKeyUp={this.handleKeyUpEvent.bind(this)}
        />

        <i className='iconfont icon-search' />

        <input
          type='button'
          value=''
          className='search-btn'
          onClick={this.searchEvent.bind(this, searchType, searchText, 0)}
        />
      </div>
    )
  }
}

export default Search
