import React, {Component} from 'react'

import './index.css'

import Header from '../../components/header'
import Content from './content'

class DetailPage extends Component {
  // 
  showSearchPage () {
    this.props.showSearchPage()
  }

  render () {
    const detailPageInfo = this.props.detailPageInfo // 
    const activeSearchPage = this.props.activeSearchPage // 
    const pageState = this.props.pageState // 

    return (
      <div>
        {/* Detailpage Header Content */}
        <Header
          activeSearchPage={activeSearchPage}
          showSearchPage={this.showSearchPage.bind(this)}
          searchPageScrollY={this.props.searchPageScrollY}
        />

        <Content
          activeSearchPage={activeSearchPage}
          detailPageInfo={detailPageInfo}
          pageState={pageState}
        />
      </div>
    )
  }
}

export default DetailPage
