import React, { Component } from 'react'
import CONFIG from '../../../../CONFIG'
import {getActivePageName} from '../../../util/util'

import './index.css'

import Item from './item'

class Footer extends Component {
  constructor () {
    super()
    this.state = {
      activeItem: getActivePageName() // item
    }
  }

  // item
  setActiveItem (item) {
    this.setState({
      activeItem: item
    })
  }

  // footer item
  setScrollY () {
    const key = this.state.activeItem
    const scrollY = this.props.scrollY

    window.scrollTo(0, scrollY[key])
  }

  render () {
    const setPage = this.props.setPage // 

    const page = CONFIG.page // searchPagepage
    const activeItem = this.state.activeItem // item
    const setActiveItem = this.setActiveItem // item

    return (
      <div className='footer'>
        {/* Footer Item */}
        {page.map((item, index) => {
          return (
            <Item
              key={index}
              page={item}
              activeItem={activeItem}
              setActiveItem={setActiveItem.bind(this)}
              setPage={setPage.bind(this)}
              setScrollY={this.setScrollY.bind(this)}
            />
          )
        })}
      </div>
    )
  }
}

export default Footer
