import React, {Component} from 'react'
import LazyLoad from 'react-lazyload'
import CONFIG from '../../../../CONFIG'
import {getPageInfo} from '../../../util/util'

import './index.css'

class Item extends Component {
  // , 
  handleClick (item) {
    this.props.showDetailPage(item)
  }

  render () {
    const activePage = getPageInfo(this.props.activePage).pageName // 
    const item = this.props.item // item
    // 
    const img = item.image || item.images.small // 
    const title = item.title || '' // title
    const tags = item.tags || [] // tags
    const rating = item.rating.average || '' // rating

    //  
    switch (activePage) {
      case CONFIG.page[0].pageName:
        const authorArr = item.author.length ? item.author : ['']
        const author = authorArr.length > 1 ? `${authorArr[0]} ` : authorArr[0]
        const pubdate = item.pubdate || ''

        return (
          <div
            className='item-container--book'
            onClick={this.handleClick.bind(this, item)}
          >

            <div className='item-img-wrapper'>
              <LazyLoad height={'100%'}>
                <img src={img} alt='' className='item-img' />
              </LazyLoad>
            </div>

            <div className='item-info-wrapper'>
              <span className='item-info-tt'>: {title}</span>

              <div className='item-info-tags'>
                {tags.map((item, index) => {
                  if (index < 3) {
                    return (
                      <span className='tags-item' key={index}>
                        {item.name}
                      </span>
                    )
                  }
                })
                }
              </div>

              <span className='item-info-author'>: {author}</span>

              <span className='item-info-rating'>: {rating}</span>

              <span className='item-info-rating'>: {pubdate}</span>
            </div>
          </div>
        )

      case CONFIG.page[1].pageName:
        const genres = item.genres || [] // genres
        const casts = item.casts || [] // 

        return (
          <div
            className='item-container--movie'
            onClick={this.handleClick.bind(this, item)}
          >

            <div className='item-img-wrapper'>
              <LazyLoad height={'100%'}>
                <img src={img} alt='' className='item-img' />
              </LazyLoad>
            </div>

            <div className='item-info-wrapper'>
              <span className='item-info-tt'>: {title}</span>

              <div className='item-info-tags'>
                {genres.map((item, index) => {
                  if (index < 3) {
                    return (
                      <span className='tags-item' key={index}>
                        {item}
                      </span>
                    )
                  }
                })}
              </div>

              <div className='item-info-casts'>
                {casts.map((item, index) => {
                  return (
                    <span className='casts-item' key={index}>
                      {item.name}
                    </span>
                  )
                })}
              </div>

              <span className='item-info-rating'>: {rating}</span>
            </div>
          </div>
        )

      case CONFIG.page[2].pageName:
        const authorMusic = item.author ? item.author[0] : {name: ''}

        return (
          <div
            className='item-container--music'
            onClick={this.handleClick.bind(this, item)}
          >

            <div className='item-img-wrapper'>
              <LazyLoad height={'100%'}>
                <img src={img} alt='' className='item-img' />
              </LazyLoad>
            </div>

            <div className='item-info-wrapper'>
              <span className='item-info-tt'>: {title}</span>

              <div className='item-info-tags'>
                {tags.map((item, index) => {
                  if (index < 3) {
                    return (
                      <span className='tags-item' key={index}>
                        {item.name}
                      </span>
                    )
                  }
                })
                }
              </div>

              <span className='item-info-author'>: {authorMusic.name}</span>

              <span className='item-info-rating'>: {rating}</span>
            </div>
          </div>
        )

      default:
        return (
          <div>~~</div>
        )
    }
  }
}

export default Item
